import "bootstrap/dist/css/bootstrap.css";
import {
  GoogleAuthProvider,
  getAuth,
  signInWithRedirect,
  signOut,
} from "firebase/auth";
import { useState, useEffect } from "react";
import { useSearchParams } from "react-router-dom";
import { app } from "./config";
import Roles from "./Roles";

const auth = getAuth(app);

const googleProvider = new GoogleAuthProvider();

const App = () => {
  const [user, setUser] = useState(null);
  const [discordID, setDiscordID] = useState("");
  const [searchParams, setSearchParams] = useSearchParams();

  console.log(auth?.currentUser)

  useEffect(() => {
    if (searchParams.has('discord-id')) {
      setDiscordID(searchParams.get('discord-id'));
      searchParams.delete('discord-id');
      setSearchParams(searchParams);
    }
  }, []);


  useEffect(() => {
    auth.onAuthStateChanged((userObj) => {
      userObj ? setUser(userObj) : signInWithRedirect(auth, googleProvider);
    });
  });

  if (!discordID)
    return (
      <div>
        Invalid Discord ID Provided. Please Contact staff on the ACIT Discord
        Server!
      </div>
    );

  if (user && !user.email.endsWith("@student.acitech.org")) {
    signOut(auth);
    return <></>;
  }

  return user ? (
    <Roles name={user?.displayName} discordID={discordID} email={user?.email} />
  ) : (
    <div>Loading...</div>
  );
};

export default App;
