import "bootstrap/dist/css/bootstrap.css";
import { database } from "./config";
import { ref, set } from "firebase/database";
import { useState, Fragment } from "react";
import { Dropdown, Button } from "react-bootstrap";
import "./Roles.scss";

import Finished from "./Finished";

const Roles = ({ discordID, email, name }) => {
  const [pronouns, setPronouns] = useState([]);
  const [academy, setAcademy] = useState("");
  const [grade, setGrade] = useState("");
  const [disabled, setDisabled] = useState(true);
  const [submitted, setSubmitted] = useState(false);

  const validPronouns = [
    "He/Him",
    "She/Her",
    "They/Them",
    "He/Them",
    "She/Them",
    "It/Its",
    "Ask For Pronouns",
  ];

  const validAcademies = [
    "AMES",
    "Computer Science",
    "Medical Assisting",
    "Dental Assisting",
    "Health Sciences",
    "CADD",
    "Graphic Design",
    "Culinary Arts",
    "Cosmetology",
    "Fashion Design",
    "Performing Arts",
    "Aviation Studies",
    "JROTC",
    "Automotive Tech",
  ];

  const validGrades = ["Freshman", "Sophomore", "Junior", "Senior", "Graduate"];

  const handlePronounChange = (ev) => {
    pronouns.indexOf(ev.target.value) === -1
      ? setPronouns([...pronouns, ev.target.value])
      : setPronouns(pronouns.filter((p) => p !== ev.target.value));
  };

  const handleAcademyChange = (ev) => {
    setAcademy(ev.target.value);
    setDisabled(!academy && !grade);
  };

  const handleGradeChange = (ev) => {
    setGrade(ev.target.value);
    setDisabled(!academy && !grade);
  };

  const handleSubmit = (ev) => {
    ev.preventDefault();
    setSubmitted(true);

      set(ref(database, "users/" + discordID), {
        name,
        email,
        pronouns,
        academy,
        grade
      })
  };

  if (submitted) return <Finished />;

  return (
    <div className="container-fluid w-100">
      <div className="flex-row w-100 py-1">
        <div className="btn-group d-flex justify-content-center">
          <Dropdown className="mx-2">
            <Dropdown.Toggle
              className="title"
              variant="outline-danger"
              id="dropdown-basic">
              Select Pronouns
            </Dropdown.Toggle>

            <Dropdown.Menu className="p-0">
              <div className="btn-group btn-group-vertical w-100" role="group">
                {validPronouns.map((pronoun, index) => (
                  <Fragment key={"pronoun-" + index}>
                    <input
                      type="checkbox"
                      className="btn-check"
                      id={"pronoun-opt-" + index}
                      value={pronoun}
                      onChange={handlePronounChange}
                    />
                    <label
                      className="btn btn-outline-primary title"
                      htmlFor={"pronoun-opt-" + index}>
                      {pronoun}
                    </label>
                  </Fragment>
                ))}
              </div>
            </Dropdown.Menu>
          </Dropdown>
          <Dropdown className="mx-2">
            <Dropdown.Toggle
              className="title"
              variant="outline-primary"
              id="dropdown-basic">
              Select Academy
            </Dropdown.Toggle>
            <Dropdown.Menu className="p-0">
              <div className="btn-group btn-group-vertical w-100" role="group">
                {validAcademies.map((academy, index) => (
                  <Fragment key={"academy-" + index}>
                    <input
                      type="radio"
                      name="academy"
                      className="btn-check"
                      id={"academy-opt-" + index}
                      value={academy}
                      onChange={handleAcademyChange}
                    />
                    <label
                      className="btn btn-outline-primary title"
                      htmlFor={"academy-opt-" + index}>
                      {academy}
                    </label>
                  </Fragment>
                ))}
              </div>
            </Dropdown.Menu>
          </Dropdown>
          <Dropdown className="mx-2">
            <Dropdown.Toggle
              className="title"
              variant="outline-info"
              id="dropdown-basic">
              Select Grade
            </Dropdown.Toggle>
            <Dropdown.Menu className="p-0">
              <div className="btn-group btn-group-vertical w-100" role="group">
                {validGrades.map((grade, index) => (
                  <Fragment key={"grade-" + index}>
                    <input
                      type="radio"
                      name="grade"
                      className="btn-check"
                      id={"grade-opt-" + index}
                      value={grade}
                      onChange={handleGradeChange}
                    />
                    <label
                      className="btn btn-outline-primary title"
                      htmlFor={"grade-opt-" + index}>
                      {grade}
                    </label>
                  </Fragment>
                ))}
              </div>
            </Dropdown.Menu>
          </Dropdown>
        </div>
      </div>
      <div className="flex-row d-flex justify-content-center">
        <Button
          variant="outline-success"
          className="title"
          disabled={disabled}
          onClick={handleSubmit}>
          Finish
        </Button>
      </div>
    </div>
  );
};

export default Roles;
